.container{
    .content{
        .single-post{
            padding-bottom: 100px;
            .categories{
                text-align: center;
                font-size: 1.2rem;
				
            }
            img{
                max-width: 90%;
                display: block;
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 10px;
                margin-top: 20px;
            }
			img + em {
				text-align:center;
				font-style: normal;
				margin-bottom: 20px;
			}
			
            p{
                font-size: 1.0rem;
				
            }
            h2{
                text-align: center;
				line-height: 1.5;
				margin-top: 30px;
            }
            h3{
                text-align: center;
                margin-top: 30px;
            }
			
			h4{
				text-align:left;
				margin-left:0;
				margin-bottom:0px;
			}
			
            a{
                text-decoration: none;
                color: black;
                display: inline-block;
                font-style: italic;
                border-bottom: 1px solid $base-color;
                line-height: 0.9;
                transition: background-color 0.1s cubic-bezier(.33,.66,.66,1);
            }
            a:hover{
                background-color: rgba(1,160,228,0.3);
                border-bottom: none;
            }
            .video{
                display: block;
                margin-left: auto;
                margin-right: auto;
                width: 50%; 
                margin-bottom: 30px;
                height: 0;
                overflow: hidden;
                padding-bottom: 29.25%;
                padding-top: 30px;
                position: relative;
                iframe, object, embed{
                    height: 100%;
                    left: 0;
                    position: absolute;
                    top: 0;
                    width: 100%;
                }
            }

        }
    }
}