$base-color: #01a0e4;
$light-cyan: #eaf7fc;
$headcolor: #ceeffc;
$darker-color: rgba(0, 160, 227, 0.2);

* {
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
            margin: 0px;
            padding: 0px;
            font-family:  'Verdana', 'Geneva', sans-serif;
}
html{
    scroll-behavior: smooth;
}

a{
    text-decoration: none;
}
p{
    a{
        display: inline-block;
        font-style: italic;
        border-bottom: 1px solid $base-color;
        line-height: 1;
        transition: background-color 0.1s cubic-bezier(.33,.66,.66,1);
    }
    a:hover{
        background-color: $darker-color;
        border-bottom: none;
    }
}

ul {
  display: block; 
  list-style-type: disc; 
  padding-left: 0px; 
  margin: 0.5em 0;
}

ul { ul {
  display: block; 
  list-style-type: disc; 
  padding-left: 20px; 
  margin: 0.5em; 
}}
.container{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    .content{
        order: 1;
        width: 55%;
    }

}


footer{
    text-align: center;
    font-size: .85rem;
    margin-bottom: 10px;
    margin-top: 20px;
    a{
        text-decoration: none;
        color: black;
    }
}

.chaptertablestyle{
	th, td {
		padding-left:10px
		}
	tr:hover {
	background-color: $headcolor}
	}

.mbtablestyle{
    border-collapse:collapse;
	> table, td, th {
	    padding: 10px}
	th {
		background-color: $headcolor
		}
	tr:nth-child(even) {
		background-color: $light-cyan;}
		}
			
			img + em {
				display: block;
				text-align:center;
			}
			